@import "./variables";

@keyframes floating {
    0% {
        transform: translateY(0);
    }
    50% {
        transform: translateY(-10px);
    }
    100% {
        transform: translateY(0);
    }
}

@keyframes scale-in {
    0% {
        transform: scale(0);
        opacity: 1;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

@keyframes animated-text {
    from {
        width: 0;
    }
    to {
        width: 50%;
    }
}

@keyframes animated-cursor{
    from {
        border-right-color: $foreground;
    }
    to {
        border-right-color: transparent;
    }
}